@import url(https://fonts.googleapis.com/css?family=Ubuntu+Condensed&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Ubuntu Condensed", sans-serif;
}
body {
  text-align: center;
  background-color: #1470b3;
  color: #ffffff;
  font-size: 1.2rem;
}
.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

header {
  font-size: 2rem;
  margin: 2%;
}
.select {
  margin: 1%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.input {
  margin: 1%;
  justify-content: center;
  display: flex;
  flex-direction: column;
}

.group {
  display: flex;
  justify-content: center;
  align-items: center;
}

i {
  font-size: 2.5rem;
  position: fixed;
  right: 4%;
  bottom: 4%;
  -webkit-animation-name: shiver;
          animation-name: shiver;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  cursor: pointer;
}
i:hover {
  -webkit-animation-name: none;
          animation-name: none;
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}

@-webkit-keyframes shiver {
  0% {
    right: 4%;
  }
  90% {
    right: 4%;
  }
  91% {
    right: 5%;
  }
  92% {
    right: 3.5%;
  }
  93% {
    right: 4.5%;
  }
  94% {
    right: 3.5%;
  }

  95% {
    right: 4.5%;
  }
  96% {
    right: 3.5%;
  }
  97% {
    right: 4.5%;
  }
  98% {
    right: 3.5%;
  }
  99% {
    right: 4%;
  }
  100% {
    right: 4%;
  }
}

@keyframes shiver {
  0% {
    right: 4%;
  }
  90% {
    right: 4%;
  }
  91% {
    right: 5%;
  }
  92% {
    right: 3.5%;
  }
  93% {
    right: 4.5%;
  }
  94% {
    right: 3.5%;
  }

  95% {
    right: 4.5%;
  }
  96% {
    right: 3.5%;
  }
  97% {
    right: 4.5%;
  }
  98% {
    right: 3.5%;
  }
  99% {
    right: 4%;
  }
  100% {
    right: 4%;
  }
}

@media screen and (max-width: 450px) {
  body {
    font-size: 1rem;
  }
  header {
    font-size: 1.5rem;
  }
  .select {
    flex-direction: column;
    align-items: center;
  }
}

@media screen and (orientation: portrait) {
  header {
    margin: 5%;
  }

  .input {
    margin: 5%;
  }
}

.select label,
.select select {
  margin: 3px;
}
select {
  cursor: pointer;
}

.box {
  position: relative;
  margin: 1% 0;
}

.box input {
  color: #ffffff;
  font-size: 1.2rem;
  margin: 1%;
  width: 250px;
  background: none;
  padding: 2% 2% 2% 1%;
  display: block;
  border: none;
  border-radius: 0;
  border-bottom: 1px solid #ffffff;
}

.box input {
  outline: none;
}

.box label {
  font-size: 1.2rem;
  font-weight: normal;
  position: absolute;
  pointer-events: none;
  left: 5px;
  top: 10px;
  -webkit-transition: 300ms ease all;
  transition: 300ms ease all;
}

.box input:focus ~ label,
.box input:valid ~ label {
  color: #1470b3;
  top: -14px;
  font-size: 0rem;
}

@media screen and (max-width: 740px) {
  .box label {
    font-size: 1rem;
    left: 5px;
    top: 6px;
  }

  .box input {
    font-size: 1rem;
    width: 220px;
  }
}

@media screen and (max-width: 450px) {
  .box input {
    width: 120px;
  }
}

ul {
  list-style: none;
}

li {
  padding: 0.5% 0;
}

.info {
  /* padding: 4%; */
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  min-height: 100vh;
  z-index: 100;
  background-color: #1470b3;
}
.info p {
  line-height: 2.5rem;
}
.info h1 {
  font-size: 2rem;
  margin: 2%;
}

@media screen and (max-width: 450px) {
  .info h1 {
    font-size: 1.5rem;
  }
}

@media screen and (orientation: portrait) {
  .info h1 {
    margin: 5%;
  }
}

